.banner {
  width: 100vw;
  height: 100vh;
  position: relative;
  overflow: hidden;
  background-color: black;
}

.banner-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  display: block;
  filter: blur(2px) contrast(0.8) brightness(0.9);
}

.banner-overlay {
  position: relative;
  z-index: 1;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

.banner-cosplay {
  position: absolute;
  left: 75%;
  bottom: 0;
  transform-origin: center bottom;
  display: block;
  z-index: 6;
  pointer-events: none;
}

.banner-cosplay-img {
  display: block;
  width: auto;
  max-width: none;
  height: auto;
}

.banner-cosplay {
  will-change: transform;
}

/* Banner Content */

.banner-content {
  position: absolute;
  left: 11%;
  top: 55%;
  transform: translateY(-50%);
  z-index: 3;
  color: white;
  pointer-events: auto;
  width: 600px;
}

/* Banner Content - Links */

a.banner-content-link {
  color: inherit;
  text-decoration: none;
  display: block;
  transition: color 280ms ease;
}

a.banner-content-link:visited {
  color: inherit;
}

.banner-content-link-inner {
  display: block;
  width: 100%;
  text-decoration: none;
  cursor: pointer;
}

.banner-content-link-inner h2 {
  color: inherit;
  text-decoration: none;
  font-size: 1.8rem;
  font-family: 'UAV-OSD-Mono-Regular', serif;
  margin: 0;
  transition: color 280ms ease;
}

/* Banner Content - Socials */

.banner-sns-icons {
  margin-top: 10px;
  display: flex;
}

.banner-sns-icon {
  width: 1.5rem;
  height: 1.5rem;
  padding: 10px;
  opacity: 0.8;
}

.banner-sns-icon:hover {
  opacity: 1;
  cursor: pointer;
  transform: scale(1.2);
  transition: ease-out 0.4s;
}

/* Banner Title */

.banner-title {
  position: absolute;
  left: 11%;
  top: 50%;
  transform: translateX(-50%);
  z-index: 4;
  color: white;
  font-size: 4.5rem;
  font-family: 'StretchPro-Regular', sans-serif;
  pointer-events: none;
}

/* Banner Header */
.banner-header {
  position: absolute;
  top: 5%;
  left: 11%;
  height: 15px;
  z-index: 5;
  padding: 10px 50px;
  border: 3px solid white;
  overflow-x: hidden;
}

/* Banner Header - Nav */
.banner-nav {
  display: flex;
  align-items: center;
  gap: 5rem;
  height: 100%;
}

.banner-nav-item {
  margin: 0;
  font-size: 0.8rem;
  line-height: 1;
}

.banner-nav-link {
  color: white;
  font-family: 'UAV-OSD-Mono-Regular', monospace;
  text-decoration: none;
  transition: color 200ms ease, opacity 200ms ease;
}

.banner-nav-link:visited {
  color: white;
}

.banner-nav-link:hover,
.banner-nav-link:focus {
  color: #ddd;
  outline: none;
}
